import React from 'react';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import ImagePopup from './ImagePopup';
import PopupWithForm from './PopupWithForm';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import api from '../utils/api.js';
import EditProfilePopup from './EditProfilePopup.js';
import EditAvatarPopup from './EditAvatarPopup.js';
import AddPlacePopup from './AddPlacePopup.js';
import InfoTooltipManage from './InfoTooltipManage.js';
import InfoTooltipMistake from './InfoTooltipMistake.js';
import { Route, Switch, Redirect, useHistory } from 'react-router-dom';
import Register from './Register';
import Login from './Login';
import * as auth from '../auth.js';
import ProtectedRoute from './ProtectedRoute';

function App() {
  const [currentUser, set–°urrentUser] = React.useState({}); //–ø–æ–ª—É—á–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± –∞–≤—Ç–æ—Ä–µ
  const [cards, setCards] = React.useState([]); //—Å—Ç–µ–π—Ç —Å –º–∞—Å—Å–∏–≤–æ–º –∫–∞—Ä—Ç–æ—á–µ–∫
  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false); // –•—É–∫, —É–ø—Ä–∞–≤–ª—è—é—â–∏–π –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ–º –ø–æ–ø-–∞–ø–∞ "–î–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è"
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false); // –•—É–∫, —É–ø—Ä–∞–≤–ª—è—é—â–∏–π –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ–º –ø–æ–ø-–∞–ø–∞ "–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏"
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false); // –•—É–∫, —É–ø—Ä–∞–≤–ª—è—é—â–∏–π –≤–Ω—É—Ç—Ä–µ–Ω–Ω–∏–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ–º –ø–æ–ø-–∞–ø–∞ "–ò–∑–º–µ–Ω–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞"
  const [selectedCard, setSelectedCard] = React.useState(false); //–•—É–∫ –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –Ω–∞–ª–∏—á–∏—è CSS-–∫–ª–∞—Å—Å–∞ –≤–∏–¥–∏–º–æ—Å—Ç–∏
  const [showImage, setShowImage] = React.useState({}); //–•—É–∫ –¥–ª—è –∑–∞—Ö–≤–∞—Ç–∞ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∫–∞—Ä—Ç–æ—á–∫—É
  const [isInfoTooltip, setInfoTooltip] = React.useState(false);
  const [isInfoTooltipMistake, setInfoTooltipMistake] = React.useState(false);
  const [loggedIn, setLoggedIn] = React.useState(false);
  const [userData, setUserData] = React.useState({});
  const history = useHistory();
  
  React.useEffect(() => {
    api.getUserInfoServer('/users/me') //–ø–æ–ª—É—á–∞–µ–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ —Å —Å–µ—Ä–≤–µ—Ä–∞
      .then((data) => {
        set–°urrentUser(data);
      })
      .catch(err => console.log(err));

    api.getInitialCards('/cards') //–ø–æ–ª—É—á–∞–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
      .then((arr) => {
        setCards(arr); //–¥–æ–±–∞–≤–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ —Å—Ç–µ–π—Ç
      })
      .catch(err => console.log(err));
  }, []);

  function handleCardClick(data) {
    setSelectedCard(true);
    setShowImage(data);
  }

  function handleEditProfileClick() { //–æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø-–∞–ø–∞ "–î–∞–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è"
    setIsEditProfilePopupOpen(true);
  }

  function handleAddPlaceClick() { //–æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø-–∞–ø–∞ "–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏"
    setIsAddPlacePopupOpen(true);
  }

  function handleEditAvatarClick() { //–æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø-–∞–ø–∞ "–ò–∑–º–µ–Ω–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞"
    setIsEditAvatarPopupOpen(true);
  }

  function closeAllPopups() {  //–æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø-–∞–ø–æ–≤
    setIsEditProfilePopupOpen(false);
    setIsAddPlacePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setSelectedCard(false);
    setInfoTooltip(false);
    setInfoTooltipMistake(false);
  }

  function handleSetInfoTooltip() { //–æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø-–∞–ø–∞ –ø–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
    setInfoTooltip(true);
  }

  function handleSetInfoTooltipMistake() { //–æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø-–∞–ø–∞ –ø–æ—Å–ª–µ –Ω–µ—É–¥–∞—á–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
    setInfoTooltipMistake(true);
  }

  function handleCardLike(card) {
    // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
    const isLiked = card.likes.some(item => item._id === currentUser._id);
    (!isLiked ? api.putLikeCard(`/cards/likes/${card._id}`) : api.deleteLikeCard(`/cards/likes/${card._id}`))
      .then((newCard) => {
        const newCards = cards.map((c) => c._id === card._id ? newCard : c);
        setCards(newCards);
      })
      .catch(err => console.log(err));
  }

  function handleCardDelete(card) { //—É–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
    api.deleteCard(`/cards/${card._id}`)
      .then(() => {
        const newCards = cards.filter((item) => item._id !== card._id);
        setCards(newCards); //–æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å—Ç–µ–π—Ç–∞
      })
      .catch(err => console.log(err));
  }

  function handleUpdateUser(data) { // –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
    api.sendUserInfo('/users/me', data)
      .then((res) => {
        set–°urrentUser(res);  //–æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å—Ç–µ–π—Ç–∞
        closeAllPopups();
      })
      .catch(err => console.log(err));
  }

  function handleUpdateAvatar(data) { // –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ –∞–≤–∞—Ç–∞—Ä–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    api.changeAvatar('/users/me/avatar', data)
      .then((res) => {
        set–°urrentUser(res); //–æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å—Ç–µ–π—Ç–∞
        closeAllPopups();
      })
      .catch(err => console.log(err));
  }

  function handleAddPlace(data) { // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏
    api.sendCard('/cards', data)
      .then((newCard) => {
        setCards([newCard, ...cards]); //–¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏ –≤ –º–∞—Å—Å–∏–≤
        closeAllPopups();
      })
      .catch(err => console.log(err));
  }

  React.useEffect(() => { //–∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø-–∞–ø–æ–≤ –Ω–∞–∂–∞—Ç–∏–µ–º –Ω–∞ –∫–ª–∞–≤–∏—à—É 'Escape'
    window.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        closeAllPopups();
      }
    });
  }, []);

  React.useEffect(() => {
    tokenCheck();
  }, [loggedIn])
  

  function handleLogin(email, password) {
 
  auth.authorize(email, password)
      .then((data) => {
        if (!data) {
          return console.log('–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫');
        }
        if (data) {
          setLoggedIn(true);
          history.push('/'); // –ø–µ—Ä–µ–∞–¥—Ä–µ—Å—É–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
          return;
        }
      })
      .catch(err => console.log(err)); // –∑–∞–ø—É—Å–∫–∞–µ—Ç—Å—è, –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –Ω–∞–π–¥–µ–Ω 
  }

  function handleRegister(email, password) {

    auth.register(email, password).then((res) => {
      if (res) {
        handleSetInfoTooltip(); //–ø–æ–ø-–∞–ø —É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
        history.push('/sign-in');
      } else {
        handleSetInfoTooltipMistake(); //–ø–æ–ø-–∞–ø –Ω–µ—É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
        console.log('–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞.');
      }
    })
    .catch(err => console.log(err));
  }

  function tokenCheck() {

    if (localStorage.getItem('jwt')) {
      const jwt = localStorage.getItem('jwt');
      auth.checkToken(jwt)
        .then((res) => {
          if (res) {
            setUserData({
              _id: res.data._id,
              email: res.data.email
            })
            setLoggedIn(true);
            history.push("/");
          }
        })
        .catch((err) => console.log(err));
    }
  }

  function signOut() {

    setLoggedIn(false);
    setUserData('');
      localStorage.removeItem('jwt');
      history.push('/sign-in');
    }
  
  return (
    <>
      <div className="page">
        <CurrentUserContext.Provider value={currentUser}>
          <Header
            loggedIn={loggedIn}
            userData={userData}
            setUserData={setUserData}
            onSignOut={signOut}
          />
          <Switch>
            <ProtectedRoute exact path="/" loggedIn={loggedIn}
              component={Main}
              onEditProfile={handleEditProfileClick}
              onAddPlace={handleAddPlaceClick}
              onEditAvatar={handleEditAvatarClick}
              onCardClick={handleCardClick}
              cards={cards}
              onCardLike={handleCardLike}
              onCardDelete={handleCardDelete}
            />
            <Route path="/sign-up">
              <Register
                onRegister={handleRegister}
                />
            </Route>
            <Route path="/sign-in">
              <Login onLogin={handleLogin}
                 />
            </Route>
            <Route>
              {loggedIn ? <Redirect to="/" /> : <Redirect to="/sign-in" />}
            </Route>
          </Switch>
          <InfoTooltipManage isOpen={isInfoTooltip} onClose={closeAllPopups} />
          <InfoTooltipMistake isOpen={isInfoTooltipMistake} onClose={closeAllPopups} />
          <EditAvatarPopup isOpen={isEditAvatarPopupOpen} onClose={closeAllPopups} onUpdateAvatar={handleUpdateAvatar} />
          <EditProfilePopup isOpen={isEditProfilePopupOpen} onClose={closeAllPopups} onUpdateUser={handleUpdateUser} />
          <AddPlacePopup isOpen={isAddPlacePopupOpen} onClose={closeAllPopups} onAddPlace={handleAddPlace} />
          <PopupWithForm name="deleting-card" title="–í—ã —É–≤–µ—Ä–µ–Ω—ã?" />
          <ImagePopup card={showImage} isOpen={selectedCard} onClose={closeAllPopups} />
          <Footer />
        </CurrentUserContext.Provider>
      </div>
    </>
  );
}

export default App;
