
const baseUrl = 'https://mesto.nomoreparties.co/v1/cohort-12'; //–±–∞–∑–æ–≤—ã–π URL
const authorization = 'b6efac6e-fe72-4acc-8171-d974e56a542c'; //–º–æ–π –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä

class Api {
  constructor({ baseUrl, authorization }) {
    this._baseUrl = baseUrl;
    this._authorization = authorization;
  }

  _fetch(url, params) {
    
    params.headers = {  
      authorization: this._authorization,
      'Content-Type': 'application/json',
    };
    return fetch(this._baseUrl + url, params)
  
      .then((res) => {
       
        if (!res.ok) {
          
          return Promise.reject(res.status);
        } else {
      
          return res.json();
        }
      })
  }

  //–º–µ—Ç–æ–¥ –ø–æ–ª—É—á–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
  getInitialCards(url) {
    return this._fetch(url, {
      method: 'GET'
    })
  }

  //–º–µ—Ç–æ–¥ –ø–æ–ª—É—á–µ–Ω–∏—è –∏–º–µ–Ω–∏ –∏ —Ä–æ–¥–∞ –¥–µ—è—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —Å —Å–µ—Ä–≤–µ—Ä–∞
  getUserInfoServer(url) {
    return this._fetch(url, {
      method: 'GET'
    })
  }

  //–º–µ—Ç–æ–¥ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ –Ω–∞ —Å–µ—Ä–≤–µ—Ä –∏ –µ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ
  sendUserInfo(url, data) {
    return this._fetch(url, {
      method: 'PATCH',
      body: JSON.stringify({
        name: `${data.name}`,
        about: `${data.about}`
      })
    })
  }

  //–º–µ—Ç–æ–¥ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
  sendCard(url, data) {
    return this._fetch(url, {
      method: 'POST',
      body: JSON.stringify({
        name: `${data.name}`,
        link: `${data.link}`
      })
    })
  }

  //–º–µ—Ç–æ–¥ —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
  deleteCard(url) {
    return this._fetch(url, {
      method: 'DELETE'
    })
  }

  //–º–µ—Ç–æ–¥ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –ª–∞–π–∫–∞
  putLikeCard(url) {
    return this._fetch(url, {
      method: 'PUT'
    })
  }
//–º–µ—Ç–æ–¥ —É–¥–∞–ª–µ–Ω–∏—è –ª–∞–π–∫–∞
deleteLikeCard(url) {
  return this._fetch(url, {
    method: 'DELETE'
  })
}

  ///–º–µ—Ç–æ–¥ –∏–∑–º–µ–Ω–µ–Ω–∏—è –∞–≤–∞—Ç–∞—Ä–∞
  changeAvatar(url, data) {
    return this._fetch(url, {
      method: 'PATCH',
      body: JSON.stringify({
        avatar: `${data.avatar}`
      })
    })
  }

}

const api = new Api({ baseUrl, authorization, }); // —ç–∫–∑–µ–º–ø–ª—è—Ä api
export default api;
