import React from 'react';

function Card(props) {
  function handleClick() {
    props.onCardClick(props.card);
  }

  const isOwn = props.card.owner._id === props.currentUser._id;
  
  // const cardDeleteButtonClassName = ( // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è –≤ className –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  //   `card__trash-button ${isOwn ? '' : 'card__trash-button_hidden'}`
  // );


  const cardDeleteButtonClassName = `card__trash-button_hidden' ${ // –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è –≤ className –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
    isOwn ? "card__trash-button" : null
  }`;




  const isLiked = props.card.likes.some(item => item._id === props.currentUser._id); // –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –ª–∞–π–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  const cardLikeButtonClassName = (// –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è –≤ className –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
    `card__like-button ${isLiked ? 'card__like-button_active' : ''}`
  );

  function handleLikeClick() {
    props.onCardLike(props.card);
  }

  function handleDeleteClick() {
    props.onCardDelete(props.card);
  }
  return (
    <div className="card">
      <img className="card__image" alt="–≤–∞—à–µ —Ñ–æ—Ç–æ" id="myImg" src={props.card.link} onClick={handleClick} />
      <h2 className="card__title">{props.card.name}</h2>
      <button type="button" className={cardDeleteButtonClassName} onClick={handleDeleteClick} aria-label="–∫–æ—Ä–∑–∏–Ω–∞"></button>
      <button type="button" className={cardLikeButtonClassName} onClick={handleLikeClick} aria-label="–ª–∞–π–∫"></button>
      <h2 className="card__like-sum">{props.card.likes.length > 0 ? `${props.card.likes.length}` : 0} </h2>
    </div>
  )
}

export default Card;